body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.game-area {
  height: 100vh;
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 1fr 15vw;
}

.side-area {
  display: grid;
  grid-template-rows: 20vh repeat(4, 1fr);
}

.temp {
  border: 1px solid black;
}
body {
  height: 100vh;
}

.card-container {
  display: grid;
  grid-template-rows: 1fr 10fr 1fr 2fr 1fr;
  grid-gap: 0;
  height: 30vh;
  width: 20vw;
  border: 10px solid black;
  /*padding: 1vmin 1vmin 1vmin 1vmin;*/
}

.name-mana-container {
  display: grid;
  grid-row-start: 1;
  grid-row-end: 2;
  overflow: hidden;
  object-fit: cover;
}

.name {
  grid-column-start: 1;
  grid-column-end: span 1;
  min-height: 100%;
  min-width: 100%;
  max-height: 100%;
  max-width: 100%;
  object-fit: cover;
  overflow: hidden;
  text-align: left;
}

.cost {
  grid-column-start: 2;
  grid-column-end: span 1;
  min-height: 100%;
  min-width: 100%;
  max-height: 100%;
  max-width: 100%;
  object-fit: cover;
  overflow: hidden;
  text-align: right;
}

.image-container {
  min-height: 100%;
  min-width: 100%;
  max-height: 100%;
  max-width: 100%;
  grid-column-start: 1;
  grid-column-end: span 4;
  grid-row-start: 2;
  grid-row-end: span 1;
  object-fit: cover;
  overflow: hidden;
}

.card-image {
  display: block;
  margin: 0 auto;
  max-height: 100%;
  max-width: 100%;
  object-fit: cover;
}

.type-set-container {
  display: grid;
  grid-row-start: 3;
  grid-row-end: 4;
}

.type-container {
  grid-column-start: 1;
  grid-column-end: span 1;
  min-height: 100%;
  min-width: 100%;
  max-height: 100%;
  max-width: 100%;
  object-fit: contain;
  text-align: left;
}

.set-logo-container {
  grid-column-start: 2;
  grid-column-end: span 1;
  min-height: 100%;
  min-width: 100%;
  max-height: 100%;
  max-width: 100%;
  object-fit: contain;
  text-align: right;
}

.text-container {
  grid-column-start: 1;
  grid-column-end: -1;
  grid-row-start: 4;
  grid-row-end: 5;
  min-height: 100%;
  min-width: 100%;
  max-height: 100%;
  max-width: 100%;
  object-fit: contain;
  overflow: hidden;
  font-size: 2vmin;
}

.power-toughness-container {
  grid-column-start: 4;
  grid-column-end: -1;
  min-height: 100%;
  min-width: 100%;
  max-height: 100%;
  max-width: 100%;
  object-fit: contain;
  text-align: right;
}

